import React from 'react';
import '../assets/styles/Services.scss'  

const Services = () => (
    <section class="section__services" id="servicios" >
        <h2>Los servicios que ofrecemos</h2>
        <div class="services__container">
            <div class="services">
                <figure class="service__figure">
                    <img src="https://i.imgur.com/7GdlkHR.png" alt="previcional" class="service__figure--img" />
                </figure>
                <h3>Previcional</h3>
                <p>- Jubilación nacional.</p>
                <p>- Jubilacion Provincial.</p>
                <p>- Pensión por fallecimiento.</p>
                <p>- Retiro por invalidez.</p>
                <p>- Juicio de reajuste nacional.</p>
                <p>- Juicio de reajuste provincial.</p>
            </div>
            <div class="services">
                <figure class="service__figure">
                    <img src="https://i.imgur.com/eqg09sA.png" alt="laboral" class="service__figure--img" />
                </figure>
                <h3>Laboral</h3>
                <p>- Accidentes laborales.</p>
                <p>- Enfermedades laborales.</p>
                <p>- Despidos.</p>
                <p>- Diferencia de herencia.</p>
            </div>
            <div class="services">
                <figure class="service__figure">
                    <img src="https://i.imgur.com/NOy22el.png" alt="familia" class="service__figure--img" />
                </figure>
                <h3>Familia y sucesiones</h3>
                <p>- Sucesiones.</p>
                <p>- Testamentos.</p>
                <p>- Divorcios.</p>
            </div>
            <div class="services">
                <figure class="service__figure">
                    <img src="https://i.imgur.com/F0GYNyE.png" alt="civil y comercial" class="service__figure--img" />
                </figure>
                <h3>Civil y comercial</h3>
                <p>- Contratos en general.</p>
                <p>- Desalojos.</p>
                <p>- Responsabilidad civil de daños y perjuicios.</p>
                <p>- Accidentes de transito.</p>
            </div>
        </div>
    </section>
);

export default Services;